body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-header{
    height: 6rem;
    background-color: cornflowerblue;
    color: white;
    border-bottom: 5px solid black;
    } 
    
    .app-header h1{
        font-size: 3rem;
        line-height: 6rem;
        text-align: center;
    }
.split {
    /* height: 100%; */
    width: 50%;
    position: fixed;
    z-index: 1;
    top: 7.5rem;
    /* overflow-x: hidden; */
    /* padding-top: 20px; */
}

.list-container {
    box-sizing: border-box;
    left: 0;
    list-style: none;
    padding: 0 0 0 10px;
}
.item{
    padding: 5px;
    border: 2px solid #bbb;
    border-radius: 8px;
    min-height: 6rem;
    position: relative;
    margin-bottom: 10px;
}

p, h1{
    padding: 0;
    margin: 0;
}

p{
    font-size:1.5rem;
}
.input-field{
    border: 1px solid #aaa;
    margin-bottom: 5px;
    font-size: 1.5rem;
    
}

.item-container{
    display: flex;
    flex-direction: column;
    width: 65%;
    min-height: 6rem;
    position: absolute;
    top: 5px;
    left: 5px;
    justify-content: space-around;
    align-items: center;
}

.hide{
    display: none;
}

.button-container{
    display: flex;
    flex-direction: column;
    width: 30%;
    position: absolute;
    top: 5px;
    right: 5px;
    min-height: 6rem;
    justify-content: space-around;
    align-items: flex-start;
}

.button{
    background-color: white;
    -webkit-transition-duration: 0.4s;
            transition-duration: 0.4s;
    cursor: pointer;
    border-radius: 5px;
    font-size: 1.2rem;
}

.button-edit{
    color: black;
    border: 2px solid #e7e7e7;
}

.button-edit:hover{
    background-color: #e7e7e7
}

.button-delete{
    color: black;
    border: 2px solid #f44336;
}

.button-delete:hover{
    background-color: #f44336;
    color: white;
}

.button-save{

    color: black; 
    border: 2px solid #4CAF50;
}

.button-save:hover{
    background-color: #4CAF50;
    color: white;
}

.addForm {
    width: 50%;
    position: fixed;
    z-index: 1;
    top: 7.5rem;
    overflow-x: hidden;
    box-sizing: border-box;
    right: 0;
    padding-left: 5px;
    padding-right: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    min-height: 12rem;
}

.input-field{
    border: 1px solid #aaa;
    margin-bottom: 5px;
    font-size: 1.5rem;
    
}

.button-save{

    color: black; 
    border: 2px solid #4CAF50;
}

.button-save:hover{
    background-color: #4CAF50;
    color: white;
}

h1{
    text-align: center;
}
